*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  height: 100%; }

html {
  scroll-behavior: smooth; }

body {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  position: relative;
  background: #FFFFFF; }
  body._fixed {
    overflow: hidden; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: 400; }

a {
  display: inline-block;
  text-decoration: none; }

p {
  margin: 0; }

ul, ol, li {
  margin: 0;
  padding: 0;
  list-style: none; }

button {
  display: block;
  background: none;
  border: none;
  padding: 0;
  cursor: pointer; }

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%; }

.content {
  flex: 1 0 auto; }

.footer {
  flex: 0 0 auto; }
